'use client';

import * as React from 'react';
import { useContext } from 'react';
import { cn } from '../../lib/utils';
import { Select as SelectPrimative, SelectTrigger, SelectValue, SelectContent, SelectItem, SelectSeparator } from "../ui/select";
import { DevContext } from '../dev/DevProvider';

// ============================================================================
// Helper: Resolve $infer<...> syntax
// ============================================================================

/**
 * Parse and resolve $infer<...> syntax using the DevContext.
 */
function useResolvedExpectedType(
  expectedType: string | undefined,
  devCtx: { inferredTypes: Record<string, string>; getInferredType: (key: string) => string | undefined } | null
): string {
  // Extract inferredTypes for proper reactivity
  const inferredTypes = devCtx?.inferredTypes;
  
  return React.useMemo(() => {
    if (!expectedType) return 'any';
    
    // Check if it's $infer syntax
    if (!expectedType.startsWith('$infer<')) {
      return expectedType;
    }
    
    // Extract content between $infer< and >
    const match = expectedType.match(/^\$infer<(.+)>$/);
    if (!match) return expectedType;
    
    const content = match[1]?.trim() || '';
    
    // Check if it's an array subscription like [field1, field2]
    if (content.startsWith('[') && content.endsWith(']')) {
      const arrayContent = content.slice(1, -1);
      const fieldNames = arrayContent
        .split(',')
        .map(s => s.trim().replace(/^["']|["']$/g, ''))
        .filter(Boolean);
      
      if (!inferredTypes) {
        return `Subscribing to: ${fieldNames.join(', ')}`;
      }
      
      const types = fieldNames
        .map(name => inferredTypes[name])
        .filter((t): t is string => !!t && t.length > 0);
      
      if (types.length === 0) {
        return `Waiting for: ${fieldNames.join(', ')}`;
      }
      
      return types.length === 1 ? types[0]! : types.join(' & ');
    }
    
    // Check if it's a single field reference
    if (!content.includes('|') && /^[a-zA-Z_][a-zA-Z0-9_\-]*$/.test(content)) {
      if (!inferredTypes) {
        return `Subscribing to: ${content}`;
      }
      const resolvedType = inferredTypes[content];
      return resolvedType || `Waiting for: ${content}`;
    }
    
    // It's a publish mode with type union
    return content;
  }, [expectedType, inferredTypes]);
}

// ============================================================================
// Types (must match packages/ui/src/components/fields/Select.tsx)
// ============================================================================

export interface SelectOption {
  node?: React.ReactNode;
  value: string;
  label: string;
}

export interface SelectProps {
  /** 
   * Field name/key. 
   * Used for Yjs field path and validation error tracking.
   * When nested inside NestedFieldProvider, the parent path is auto-prepended.
   */
  fieldName: string;

  /** Display label for the field */
  label: string;

  /** Current value */
  value: string | { expression: string; type: 'expression'; value?: string };

  /** Called when value changes */
  onChange: (
    value: string | { expression: string; type: 'expression' },
    metadata?: any
  ) => void;

  /** Available options */
  options: SelectOption[] | string[];

  /** Whether the field is disabled */
  disabled?: boolean;

  /** Placeholder text */
  placeholder?: string;

  /** Expected return type for validation */
  expectedType?: string;

  /** Whether this is a required field */
  required?: boolean;

  /** Whether this field has a required property error (missing value) */
  hasRequiredError?: boolean;

  /** Custom className for the wrapper */
  className?: string;

  /** Property definition (for richer context) */
  propertyDefinition?: any;

  /** 
   * Render prop for custom select UI.
   * If not provided, uses a basic HTML select.
   */
  children?: (props: SelectRenderProps) => React.ReactNode;
}

export interface SelectRenderProps {
  value: string;
  onChange: (value: string) => void;
  onFocus?: () => void;
  onBlur?: () => void;
  onExpressionClick: () => void;
  options: SelectOption[];
  localInput: string;
  setLocalInput: (value: string) => void;
  expressionMode: {
    current: 'value' | 'expression';
    isExpressionMode: boolean;
    isEditorMode: boolean;
    isEditing: boolean;
    isFullScreen: boolean;
    expression: string;
    switchToValue: () => void;
    switchToExpression: () => void;
    switchToEditor: () => void;
    clear: () => void;
  };
  inferredType?: string;
  hasTypeMismatch?: boolean;
  hasError?: boolean;
}

// ============================================================================
// Mock Component
// ============================================================================

/**
 * Mock Select component for development/design mode.
 * 
 * This is a simplified version that renders a basic select field.
 * In production, this is replaced with the real Select from packages/ui
 * which includes collaboration, type inference, and expression support.
 * 
 * @example
 * ```tsx
 * <Select 
 *   fieldName="status" 
 *   label="Status" 
 *   value={value}
 *   onChange={setValue}
 *   options={['draft', 'published', 'archived']}
 * />
 * ```
 */
export function Select({
  fieldName,
  label,
  value,
  onChange,
  options: rawOptions,
  disabled = false,
  placeholder,
  expectedType = 'string',
  required = false,
  hasRequiredError = false,
  className,
  children,
}: SelectProps) {
  // Get DevContext for type inference resolution
  const devCtx = useContext(DevContext);
  
  // Resolve $infer<...> syntax to actual type
  const resolvedExpectedType = useResolvedExpectedType(expectedType, devCtx);
  
  const [isExpressionMode, setIsExpressionMode] = React.useState(false);
  const [expressionValue, setExpressionValue] = React.useState('');

  // Normalize options to SelectOption[]
  const options: SelectOption[] = React.useMemo(() => {
    return rawOptions.map(opt =>
      typeof opt === 'string'
        ? { value: opt, label: opt }
        : opt
    );
  }, [rawOptions]);

  // Extract the display value from expression objects
  const displayValue = React.useMemo(() => {
    if (value && typeof value === 'object' && 'expression' in value) {
      return value.expression || '';
    }
    return String(value ?? '');
  }, [value]);

  // Check if value is an expression
  const isExpression = React.useMemo(() => {
    return value && typeof value === 'object' && 'expression' in value;
  }, [value]);

  // Sync expression mode state with value
  React.useEffect(() => {
    if (isExpression) {
      setIsExpressionMode(true);
      setExpressionValue(displayValue);
    }
  }, [isExpression, displayValue]);

  const handleSelectChange = React.useCallback((newValue: string) => {
    if (newValue === '__expression__') {
      setIsExpressionMode(true);
      return;
    }
    onChange(newValue);
  }, [onChange]);

  const handleExpressionChange = React.useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setExpressionValue(newValue);
    onChange({
      expression: newValue,
      type: 'expression',
    });
  }, [onChange]);

  const handleSwitchToValue = React.useCallback(() => {
    setIsExpressionMode(false);
    setExpressionValue('');
    onChange('');
  }, [onChange]);

  const showError = hasRequiredError || (required && !displayValue);

  // Create render props for custom select UI
  const renderProps: SelectRenderProps = {
    value: displayValue,
    onChange: handleSelectChange,
    onExpressionClick: () => setIsExpressionMode(true),
    options,
    localInput: displayValue,
    setLocalInput: () => { },
    expressionMode: {
      current: isExpressionMode ? 'expression' : 'value',
      isExpressionMode,
      isEditorMode: false,
      isEditing: false,
      isFullScreen: false,
      expression: expressionValue,
      switchToValue: handleSwitchToValue,
      switchToExpression: () => setIsExpressionMode(true),
      switchToEditor: () => setIsExpressionMode(true),
      clear: handleSwitchToValue,
    },
    hasError: showError,
  };

  // Determine badge style based on resolution status
  const isWaiting = resolvedExpectedType.startsWith('Waiting for:') || resolvedExpectedType.startsWith('Subscribing to:');
  
  return (
    <div className={cn('uii:mb-2', className)}>
      {/* Header with label and type badge */}
      <div className="uii:flex uii:items-center uii:gap-2 uii:mt-2">
        <label
          htmlFor={fieldName}
          className="uii:text-xs uii:font-bold uii:text-muted-foreground"
        >
          {label}:
        </label>
        {resolvedExpectedType !== '$.interface.timer' && (
          <span 
            className={cn(
              "uii:-mt-2 uii:inline-flex uii:px-1 uii:py-0.5 uii:rounded-sm uii:text-[10px] uii:font-mono uii:font-light uii:max-w-[300px] uii:truncate",
              isWaiting 
                ? "uii:bg-yellow-100 uii:text-yellow-700" 
                : "uii:bg-gray-200 uii:text-muted-foreground"
            )}
            title={resolvedExpectedType}
          >
            {resolvedExpectedType}
          </span>
        )}
        {showError && (
          <span className="uii:-mt-2 uii:inline-flex uii:px-1 uii:py-0.5 uii:bg-red-100 uii:text-red-600 uii:rounded-sm uii:text-[10px] uii:font-medium">
            Required
          </span>
        )}
      </div>

      {/* Field content */}
      <div className="uii:mt-0.5">
        {children ? (
          // Use custom render function
          children(renderProps)
        ) : isExpressionMode ? (
          // Expression mode - show input with clear button
          <div className="uii:flex uii:items-center uii:h-9 uii:rounded-sm uii:border uii:border-amber-500/50 uii:bg-amber-500/5">
            <input
              id={fieldName}
              name={fieldName}
              type="text"
              value={expressionValue}
              onChange={handleExpressionChange}
              disabled={disabled}
              placeholder="Enter expression..."
              className={cn(
                'uii:flex-1 uii:h-full uii:px-3 uii:bg-transparent uii:text-sm uii:font-mono uii:text-amber-600',
                'uii:outline-none uii:border-none',
                disabled && 'uii:opacity-50 uii:cursor-not-allowed'
              )}
            />
            <button
              type="button"
              onClick={handleSwitchToValue}
              className="uii:flex uii:items-center uii:justify-center uii:h-full uii:px-2 uii:text-amber-600 hover:uii:text-red-600 uii:border-l uii:border-amber-500/30"
              title="Clear and return to value mode"
            >
              
            </button>
          </div>
        ) : (
          // Value mode - show select
          // <select
          //   id={fieldName}
          //   name={fieldName}
          //   value={displayValue}
          //   onChange={(e) => handleSelectChange(e.target.value)}
          //   disabled={disabled}
          //   className={cn(
          //     'uii:w-full uii:h-9 uii:px-3 uii:rounded-sm uii:border uii:bg-background uii:text-sm',
          //     'uii:outline-none uii:transition-all uii:cursor-pointer',
          //     'focus:uii:border-ring focus:uii:ring-ring/50 focus:uii:ring-[3px]',
          //     showError
          //       ? 'uii:border-red-500 hover:uii:border-red-600'
          //       : 'uii:border-input hover:uii:border-ring/50',
          //     disabled && 'uii:opacity-50 uii:cursor-not-allowed'
          //   )}
          // >
          //   {placeholder && (
          //     <option value="" disabled>
          //       {placeholder}
          //     </option>
          //   )}
          //   {options.map(opt => (
          //     <option key={opt.value} value={opt.value}>
          //       {opt.label}
          //     </option>
          //   ))}
          //   <option value="__expression__">
          //      Custom Expression
          //   </option>
          // </select>
          <SelectPrimative
      value={displayValue}
      onValueChange={(e) => handleSelectChange(e)}
      // onChange={(e) => props.onChange(e.target.value as any)}
      // onFocus={props.onFocus}
      // onBlur={props.onBlur}
      disabled={disabled ?? disabled}
    >
      <SelectTrigger  className="ui:w-full ui:h-9 ui:px-3 ui:border ui:border-input ui:rounded-sm ui:bg-background"
      >
        <SelectValue placeholder={placeholder} />
      </SelectTrigger>
      <SelectContent>
        {options.map(opt => (
          <SelectItem key={opt.value} value={opt.value}>
            {opt.node ? opt.node : <>{opt.label}</>}
          </SelectItem>
        ))}
      </SelectContent>
    </SelectPrimative>
        )}
      </div>
    </div>
  );
}

export default Select;

