export function InferredTypesProvider({ children }: InferredTypesProviderProps) {
  // In mock mode, just render children without type propagation
  return <>{children}</>;
}

/**
 * Compute the intersection of multiple types.
 * Used when subscribing to multiple fields to narrow the expected type.
 * 
 * @example
 * intersectTypes(['string | number', 'number'])  'number'
 */
export function intersectTypes(types: (string | undefined)[]): string {
  const validTypes = types.filter((t): t is string => !!t && t.length > 0);
  if (validTypes.length === 0) return 'any';
  if (validTypes.length === 1) return validTypes[0]!;
  
  // Simple mock implementation - just return the first type
  // In production, this does real intersection logic
  return validTypes[0]!;
}

/**
 * Configuration parsed from the $infer<...> syntax.
 */
export interface InferConfig {
  /** The mode of the field: 'publish', 'subscribe', or 'normal' */
  mode: 'publish' | 'subscribe' | 'normal';
  /** For publish mode: the allowed types (e.g., ['string', 'number', 'boolean']) */
  allowedTypes?: string[];
  /** For subscribe mode: the field name to subscribe to */
  subscribeToField?: string;
}

/**
 * Parse the $infer<...> syntax from an expectedType string.
 * 
 * @example
 * ```tsx
 * parseInferSyntax('$infer<string | number>') 
 * //  { mode: 'publish', allowedTypes: ['string', 'number'] }
 * 
 * parseInferSyntax('$infer<switchExpression>')
 * //  { mode: 'subscribe', subscribeToField: 'switchExpression' }
 * 
 * parseInferSyntax('string')
 * //  { mode: 'normal' }
 * ```
 */
export function parseInferSyntax(expectedType: string | undefined): InferConfig {
  if (!expectedType || !expectedType.startsWith('$infer<')) {
    return { mode: 'normal' };
  }
  
  // Extract content between $infer< and >
  const match = expectedType.match(/^\$infer<(.+)>$/);
  if (!match) {
    return { mode: 'normal' };
  }
  
  const content = match[1]?.trim() || '';
  
  // Check if it's a field reference (no pipes, looks like an identifier)
  if (!content.includes('|') && /^[a-zA-Z_][a-zA-Z0-9_]*$/.test(content)) {
    return {
      mode: 'subscribe',
      subscribeToField: content,
    };
  }
  
  // It's a type union - parse the allowed types
  const allowedTypes = content.split('|').map(t => t.trim()).filter(Boolean);
  return {
    mode: 'publish',
    allowedTypes,
  };
}

