/**
 * Props for NodePropertyProvider
 */
export interface NodePropertyProviderProps {
  children: React.ReactNode;
  /** The node ID this provider manages */
  nodeId: string;
  /** Yjs document for collaborative sync (ignored in mock mode) */
  yDoc?: unknown;
  /** Initial property data from the node */
  initialData?: Record<string, any>;
  /** Callback when a single property changes */
  onPropertyChange?: (key: string, value: any, metadata?: any) => void;
  /** Callback when multiple properties change */
  onPropertiesChange?: (updates: Record<string, any>, metadata?: Record<string, any>) => void;
  /** Client ID for conflict resolution */
  clientId?: string;
}

/**
 * Mock provider for node properties.
 * In development mode, this is a no-op - just renders children.
 * In production, the real implementation from @repo/ui provides actual store.
 */
export function NodePropertyProvider({ children }: NodePropertyProviderProps): React.ReactElement {
  return <>{children}</>;
}

/**
 * Hook to check if we're inside a property provider.
 * 
 * Returns true when inside a DevProvider or a production NodePropertyProvider.
 * Returns false otherwise.
 */
export function useIsInNodePropertyProvider(): boolean {
  const devContext = useContext(DevContext);
  return devContext !== null;
}

/**
 * Hook to access and update a single property from the node store.
 * 
 * When used within a DevProvider, uses the dev context's storage.
 * When used within a production NodePropertyProvider, uses the real store.
 * Otherwise, returns [undefined, no-op] as a fallback.
 * 
 * @example
 * ```tsx
 * const [operator, setOperator] = useNodeProperty<string>('operator');
 * ```
 */
export function useNodeProperty<T = any>(
  key: string
): [T | undefined, (value: T, metadata?: any) => void] {
  // Check if we're inside a DevProvider
  const devContext = useContext(DevContext);
  
  // If DevProvider is present, use its storage
  const [devValue, setDevValue] = useState<T | undefined>(() => 
    devContext?.data?.[key] as T | undefined
  );
  
  // Subscribe to DevProvider updates
  React.useEffect(() => {
    if (devContext) {
      setDevValue(devContext.data[key] as T | undefined);
    }
  }, [devContext, key]);
  
  const devSetter = useCallback((value: T) => {
    if (devContext) {
      devContext.setProperty(key, value);
      setDevValue(value);
    }
  }, [devContext, key]);
  
  // Fallback no-op setter
  const noopSetter = useCallback(() => {
    console.warn(`[useNodeProperty] No provider - cannot set "${key}"`);
  }, [key]);
  
  if (devContext) {
    return [devValue, devSetter];
  }
  
  return [undefined, noopSetter];
}

/**
 * Hook to access and update all properties from the node store.
 * 
 * When used within a DevProvider, uses the dev context's storage.
 * When used within a production NodePropertyProvider, uses the real store.
 * Otherwise, returns [{}, no-op] as a fallback.
 */
export function useNodeProperties(): [
  Record<string, any>,
  (updates: Record<string, any>, metadata?: Record<string, any>) => void
] {
  // Check if we're inside a DevProvider
  const devContext = useContext(DevContext);
  
  const [devData, setDevData] = useState<Record<string, any>>(() => 
    devContext?.data ?? {}
  );
  
  // Subscribe to DevProvider updates
  React.useEffect(() => {
    if (devContext) {
      setDevData({ ...devContext.data });
    }
  }, [devContext, devContext?.data]);
  
  const devSetter = useCallback((updates: Record<string, any>) => {
    if (devContext) {
      Object.entries(updates).forEach(([key, value]) => {
        devContext.setProperty(key, value);
      });
      setDevData(prev => ({ ...prev, ...updates }));
    }
  }, [devContext]);
  
  // Fallback no-op setter
  const noopSetter = useCallback(() => {
    console.warn('[useNodeProperties] No provider - cannot set properties');
  }, []);
  
  if (devContext) {
    return [devData, devSetter];
  }
  
  return [{}, noopSetter];
}

/**
 * Hook to subscribe to an inferred type by field name.
 * 
 * When used within a DevProvider, uses the dev context's inferred types.
 * When used within a production provider, uses the real store.
 * Returns undefined when not inside any provider.
 */
export function useInferredType(fieldName: string): string | undefined {
  const devContext = useContext(DevContext);
  
  const [devInferredType, setDevInferredType] = useState<string | undefined>(() =>
    devContext?.inferredTypes?.[fieldName]
  );
  
  // Subscribe to DevProvider updates
  React.useEffect(() => {
    if (devContext) {
      setDevInferredType(devContext.inferredTypes[fieldName]);
    }
  }, [devContext, fieldName, devContext?.inferredTypes]);
  
  if (devContext) {
    return devInferredType;
  }
  
  return undefined;
}

/**
 * Hook to get a setter for inferred types.
 * 
 * When used within a DevProvider, uses the dev context's setInferredType.
 * Returns a no-op when not inside any provider.
 */
export function useSetInferredType(): (fieldName: string, type: string) => void {
  const devContext = useContext(DevContext);
  
  return useCallback((fieldName: string, type: string) => {
    if (devContext) {
      devContext.setInferredType(fieldName, type);
    } else {
      console.warn(`[useSetInferredType] No provider - cannot set "${fieldName}" to "${type}"`);
    }
  }, [devContext]);
}

/**
 * Hook to get a clearer for inferred types.
 * 
 * When used within a DevProvider, uses the dev context's clearInferredType.
 * Returns a no-op when not inside any provider.
 */
export function useClearInferredType(): (fieldName: string) => void {
  const devContext = useContext(DevContext);
  
  return useCallback((fieldName: string) => {
    if (devContext) {
      devContext.clearInferredType(fieldName);
    } else {
      console.warn(`[useClearInferredType] No provider - cannot clear "${fieldName}"`);
    }
  }, [devContext]);
}

/**
 * Hook to get a function that clears all inferred types.
 * 
 * When used within a DevProvider, uses the dev context's clearAllInferredTypes.
 * Returns a no-op when not inside any provider.
 */
export function useClearAllInferredTypes(): () => void {
  const devContext = useContext(DevContext);
  
  return useCallback(() => {
    if (devContext) {
      devContext.clearAllInferredTypes();
    } else {
      console.warn(`[useClearAllInferredTypes] No provider - cannot clear all types`);
    }
  }, [devContext]);
}

/**
 * Hook to subscribe to all inferred types.
 * 
 * When used within a DevProvider, uses the dev context's inferred types.
 * Returns {} when not inside any provider.
 */
export function useAllInferredTypes(): Record<string, string> {
  const devContext = useContext(DevContext);
  
  const [devTypes, setDevTypes] = useState<Record<string, string>>(() =>
    devContext?.inferredTypes ?? {}
  );
  
  // Subscribe to DevProvider updates
  React.useEffect(() => {
    if (devContext) {
      setDevTypes({ ...devContext.inferredTypes });
    }
  }, [devContext, devContext?.inferredTypes]);
  
  if (devContext) {
    return devTypes;
  }
  
  return {};
}

/**
 * Hook to get a setter for individual properties.
 * 
 * When used within a DevProvider, uses the dev context's setProperty.
 * Returns a no-op when not inside any provider.
 */
export function useSetProperty(): <T = any>(key: string, value: T, metadata?: any) => void {
  const devContext = useContext(DevContext);
  
  return useCallback(<T = any>(key: string, value: T) => {
    if (devContext) {
      devContext.setProperty(key, value);
    } else {
      console.warn(`[useSetProperty] No provider - cannot set "${key}"`);
    }
  }, [devContext]);
}

/**
 * Hook to trigger a layout update from within a custom control.
 * 
 * Call this after structural changes that affect the node's visual layout,
 * such as reordering items or toggling features that change the node's height.
 * 
 * In development mode (DevProvider), this is a no-op.
 * In production, this triggers regenerateRfState() to recalculate the flow layout.
 * 
 * @example
 * ```tsx
 * function CaseEditor() {
 *   const triggerLayoutUpdate = useTriggerLayoutUpdate();
 *   
 *   const handleReorderCases = (newCases) => {
 *     setValue({ ...value, cases: newCases });
 *     // Trigger layout recalculation after reordering
 *     triggerLayoutUpdate();
 *   };
 * }
 * ```
 */
export function useTriggerLayoutUpdate(): () => void {
  const devContext = useContext(DevContext);
  
  return useCallback(() => {
    if (devContext) {
      // No-op in dev mode - layout updates don't apply
      console.log('[useTriggerLayoutUpdate] Dev mode - layout update is a no-op');
    } else {
      console.warn('[useTriggerLayoutUpdate] No provider - layout update ignored');
    }
  }, [devContext]);
}

/**
 * Validation rule for a field.
 */
export interface FieldValidationRule {
  required?: boolean;
  requiredIf?: (properties: Record<string, any>) => boolean;
  customValidation?: (value: any, properties: Record<string, any>) => string | null;
}

/**
 * Mock hook - returns no-ops in development mode.
 * In production, allows custom controls to set validation rules.
 * 
 * @example
 * ```tsx
 * const { setFieldRequired, setFieldRequiredIf } = useFieldValidation();
 * setFieldRequired('expression', true);
 * setFieldRequiredIf('expression', (props) => props.operator?.needsValue);
 * ```
 */
export function useFieldValidation() {
  const setFieldRequired = useCallback((fieldName: string, required: boolean) => {
    console.warn(`[useFieldValidation] Mock mode - cannot set required for "${fieldName}"`);
  }, []);
  
  const setFieldRequiredIf = useCallback(
    (fieldName: string, requiredIf: (properties: Record<string, any>) => boolean) => {
      console.warn(`[useFieldValidation] Mock mode - cannot set requiredIf for "${fieldName}"`);
    },
    []
  );
  
  const setFieldValidation = useCallback(
    (fieldName: string, customValidation: (value: any, properties: Record<string, any>) => string | null) => {
      console.warn(`[useFieldValidation] Mock mode - cannot set validation for "${fieldName}"`);
    },
    []
  );
  
  const clearFieldValidation = useCallback((fieldName: string) => {
    console.warn(`[useFieldValidation] Mock mode - cannot clear validation for "${fieldName}"`);
  }, []);
  
  const isFieldRequired = useCallback((fieldName: string): boolean => {
    return false; // Always false in mock mode
  }, []);
  
  const validateField = useCallback((fieldName: string): string | null => {
    return null; // No errors in mock mode
  }, []);
  
  return {
    setFieldRequired,
    setFieldRequiredIf,
    setFieldValidation,
    clearFieldValidation,
    isFieldRequired,
    validateField,
  };
}